body {
  background: #FFF;
  color: black;
  line-height: 1.4;
  min-width: 320px;
  font-family: "firasans-light";
  font-weight: normal;
  font-size: 1.6rem;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  text-decoration: none;
  color: #003CFF;
  transition: color 0.2s ease;
}

a:hover, a:focus {
  color: red;
}

.lnk,
a:not([class]) {
  text-decoration: none;
  border-bottom: 1px solid rgba(0, 60, 255, 0.3);
  transition: all 0.2s ease;
  transition-property: color, border-color;
}

.lnk:hover,
a:not([class]):hover {
  border-color: rgba(255, 0, 0, 0.3);
}

.lnk_black:not(:hover) {
  border-color: rgba(0, 0, 0, 0.3);
}

.out {
  overflow: hidden;
}

.text {
  font-family: "firasans-light";
  font-weight: normal;
  font-size: 1.6rem;
  line-height: 1.5625;
}

.text p {
  margin: 1em 0;
}

.text p:first-child {
  margin-top: 0;
}

.text p:last-child {
  margin-bottom: 0;
}

.text a:not(:hover) {
  color: inherit;
}

.header {
  position: fixed;
  z-index: 3;
  top: 0;
  left: 0;
  width: 100%;
  background: white;
  min-width: 320px;
  transition: box-shadow 0.3s ease;
}

.header .hero-links {
  margin: 0;
  position: absolute;
  top: 0;
  height: 100%;
  left: 0;
  right: 0;
  opacity: 0;
  background: white;
  visibility: hidden;
  transition: all 0.2s ease-out;
  overflow: hidden;
}

.header .hero-links:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  height: 100%;
  width: 0;
}

.header .hero-btn {
  transform: translateY(100%);
  transition: all 0.2s ease-out;
}

.header.has-shadow {
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
}

.header.is-show-buttons .hero-links {
  opacity: 1;
  visibility: visible;
}

.header.is-show-buttons .hero-btn {
  transform: translateY(0);
}

.header.is-show-buttons .lang {
  z-index: 0;
}

.header.is-hide-buttons .hero-links {
  opacity: 0;
  visibility: hidden;
  transition-timing-function: ease-in;
}

.header.is-hide-buttons .hero-btn {
  transform: translateY(-100%);
  transition-timing-function: ease-in;
}

@media only screen and (max-width: 767px) {
  .header .l {
    position: relative;
  }
  .header .header__inner {
    padding: 15px 0;
    position: static;
  }
  .header .hero-links {
    padding-left: 15px;
    padding-right: 15px;
  }
  .header .lang {
    top: 50%;
    margin-top: -0.65em;
    right: 70px;
  }
  .header .nav {
    visibility: hidden;
    opacity: 0;
    position: absolute;
    z-index: 3;
    top: 0;
    right: 0;
    left: 0;
    background: white;
    text-align: center;
    padding: 40px 15px 15px;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
    transition: 0.3s ease;
    transition-property: opacity, visibility;
    max-height: 100vh;
    overflow: auto;
    box-sizing: border-box;
    -webkit-overflow-scrolling: touch;
  }
  .header .nav__item {
    display: block;
    margin: 0;
  }
  .header .nav__link {
    line-height: 2;
  }
  .header.is-show-menu .logo {
    z-index: 5;
  }
  .header.is-show-menu .nav {
    opacity: 1;
    visibility: visible;
  }
}

