}

.header__buttons {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  overflow: hidden;
}

.menu-button {
  display: none;
  position: absolute;
  z-index: 5;
  top: 50%;
  right: 8px;
  margin-top: -20px;
  width: 40px;
  height: 40px;
  box-sizing: border-box;
  padding: 8px;
  color: #003CFF;
  background: none;
}

.menu-button:before, .menu-button:after,
.menu-button > span {
  content: "";
  display: block;
  width: 100%;
  border-top: 3px solid;
  transition: all 0.3s ease;
}

.menu-button > span {
  margin: 6px 0;
}

.is-show-menu .menu-button:before {
  transform: rotate(45deg);
  transform-origin: 2px 6px;
}

.is-show-menu .menu-button:after {
  transform: rotate(-45deg);
  transform-origin: 1px -3px;
}

.is-show-menu .menu-button > span {
  opacity: 0;
  transform: scale(0);
}

.menu-button:hover {
  color: red;
}

